.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  /* border-bottom: 1px solid #aaa;
  margin: 0 0 10px; */
  padding: 0;
}

.react-tabs__tab {
  display: inline-block;
  color: rgba(255, 255, 255, 0.3);
  font-size: 18px;
  font-weight: 500;
  letter-spacing: -0.36px;

  padding-right: 14px;
  /* border: 1px solid transparent; */
  /* border-bottom: none; */
  /* bottom: -1px; */
  /* position: relative; */
  /* list-style: none; */
  /* padding: 6px 12px; */
  cursor: pointer;
}

.react-tabs__tab--selected {
  color: #fff;
  font-size: 18px;
  /* font-family: Poppins; */
  font-weight: 500;
  letter-spacing: -0.36px;
}

.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}

.react-tabs__tab:focus {
  outline: none;
}

.react-tabs__tab:focus:after {
  content: '';
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #fff;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}
